import anime from 'animejs';

function scrollTo(selector) {
  let elm;

  if (typeof selector === 'string') {
    elm = document.querySelector(selector);
  } else {
    if (selector.getBoundingClientRect) {
      elm = selector.getBoundingClientRect ? selector : null;
    }
  }

  if (!elm) {
    console.warn('target element not found. selector:', selector);
    return;
  }

  const html = document.querySelector('html');
  const top = elm.getBoundingClientRect().top;
  const navHeight = document.querySelector('.main-navbar').getBoundingClientRect().height;
  const scroolOffset = top - navHeight;

  anime({
    targets: html,
    round: 1,
    scrollTop: {
      value: html.scrollTop + scroolOffset,
      duration: 200,
      easing: 'easeInOutQuad'
    }
  });
}


export {scrollTo};
