<template>
  <section class="video-section">
    <div ref="video" class="video">
      <div class="video__container">
        <app-iframe-video :url="videoUrl" />
      </div>
      <!-- <transition name="fade" mode="out-in" type="transition"> -->
      <div class="video__info" v-if="selectedVideo">
        <h1 class="video__title">{{selectedVideo.snippet.title}}</h1>
        <p class="video__description">{{selectedVideo.snippet.description}}</p>
      </div>
      <!-- </transition> -->
    </div>

    <app-swiper>
      <app-swiper-slide :key="v.id.videoId" v-for="v in videos">
        <img :src="v.snippet.thumbnails.medium.url" alt @click="selectVideo(v)" />
      </app-swiper-slide>
    </app-swiper>
  </section>
</template>

<script>
import {fetchVideos} from '@/api';
import {scrollTo} from '@/utils';


export default {
  data() {
    return {
      videoPayload: {},
      selectedVideo: null
    };
  },

  computed: {
    videos() {
      return this.videoPayload.items;
    },
    videoUrl() {
      if (!this.selectedVideo) {
        return '';
      }
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`;
    }
  },

  async mounted() {
    this.videoPayload = await fetchVideos();
    await this.$nextTick();
    this.$el.querySelector('.swiper-slide:first-child > :first-child').click();
  },

  methods: {
    selectVideo(video) {
      this.selectedVideo = null;
      this.selectedVideo = video;
      scrollTo(this.$el);
    }
  }
};
</script>

<style lang="scss" scoped>
.video-section {
}

.video {
  display: flex;
}

.video__container {
  flex-grow: 1;
}

.video__info {
  width: 40vw;
  margin-left: 2rem;
}

.video__title {
  margin-top: 0;
  margin-bottom: 2rem;
}

.video__description {
  border-top: 2px solid rgba(0, 0, 0, 0.2);
  font-size: 1.3rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.video-list {
  margin-left: auto;
  overflow-y: auto;
  overflow-x: hidden;
  width: 420px;
  flex-shrink: 0;
  max-height: 70vh;
  padding-left: 2rem;
}

.video-item {
  cursor: pointer;
  transition: 0.2s;
  display: flex;
}

.video-item:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.video-item + .video-item {
  margin-top: 1rem;
}

.video-item__thumb {
  max-width: 200px;
}

.video-item__info {
  padding-left: 1rem;
}

.video-item__title {
  margin-top: 0;
  // overflow: hidden;
  // text-overflow: ellipsis;
  // white-space: nowrap;
}

@media screen and (max-width: 768px) {
  .video {
    display: block;
  }

  .video__info {
    width: auto;
    margin-left: 0;
    margin-top: 2rem;
  }

  .video-section {
    flex-direction: column;
  }

  .video-list {
    border-top: 2px solid rgba(0, 0, 0, 0.8);
    width: 100%;
    margin: 1rem 0 0 0;
    padding: 3rem 0 0 0;
  }
}
</style>
