<template>
  <section class="stories-section">
    <h1 class="stories-title">Ëß£Èéñ‰ªñÂÄëÁöÑÊïÖ‰∫ã</h1>

    <app-swiper>
      <app-swiper-slide :key="v.id.videoId" v-for="v in videos">
        <img :src="v.snippet.thumbnails.medium.url" alt="" @click="selectVideo(v)">
      </app-swiper-slide>
    </app-swiper>

    <app-modal ref="modal">
      <div class="video-container">
        <app-iframe-video :url="videoUrl" />
      </div>
    </app-modal>
  </section>
</template>

<script>
import {fetchVideos} from '@/api';

export default {
  data() {
    return {
      videoPayload: {},
      selectedVideo: null
    };
  },

  computed: {
    videos() {
      return this.videoPayload.items;
    },
    videoUrl() {
      if (!this.selectedVideo) {
        return '';
      }
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`;
    }
  },

  async mounted() {
    this.videoPayload = await fetchVideos();
  },

  methods: {
    selectVideo(v) {
      this.selectedVideo = v;
      this.$refs.modal.toggle();
    }
  }
};
</script>

<style lang="scss" scoped>

.stories-section {
}

.stories-title {
  font-size: 2.5rem;
  text-align: center;
}

.video-container {
  width: 50vw;
}

@media screen and (max-width: 768px){
  .video-container {
    width: 90vw;
  }
}

</style>
