<template>
  <dev class="modal">
    <div @click="toggle" class="modal__close">&#10006;</div>
    <slot></slot>
  </dev>
</template>

<script>
import anime from 'animejs';

const KEYCODE_ESC = 27;

export default {
  data() {
    return {
      show: false
    };
  },

  mounted() {
    window.addEventListener('keyup', this.onKeyup);
  },

  beforeDestroy() {
    window.removeEventListener('keyup', this.onKeyup);
  },

  methods: {
    onKeyup(event) {
      event.stopPropagation();
      if (this.show && event.keyCode === KEYCODE_ESC) {
        this.toggle();
      }
    },

    toggle() {
      const html = document.querySelector('html');
      const opt = {
        targets: this.$el,
        scale: {
          value: this.show ? 0 : 1,
          duration: 200,
          easing: 'easeInOutQuad'
        },
        begin: (anim) => {
          if (!this.show) {
            this.$el.style.display = 'flex';
            html.style.overflowY = 'hidden';
          }
        },
        complete: (anim) => {
          if (this.show) {
            this.$el.style.display = 'none';
            html.style.overflowY = 'auto';
          }
          this.show = !this.show;
        }
      };
      anime(opt);
    }
  }
};
</script>

<style lang="scss" scoped>
.modal {
  position: fixed;
  z-index: 1000;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.9);
  overflow: auto;
  transform: scale(0);
  display: none;
}

.modal__close {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  top: 2rem;
  right: 2rem;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  color: white;
  transition: 0.2s;
  border-radius: 50%;
  z-index: 1100;
}

.modal__close:hover {
  background-color: rgba(255,255,255,0.3);
}
</style>
