<template>
  <div class="swiper-container">
    <!-- Additional required wrapper -->
    <div class="swiper-wrapper">
      <!-- Slides -->
      <slot></slot>
    </div>

    <!-- If we need navigation buttons -->

    <!-- <div v-show="showNavigation" ref="prevBtn" class="swiper-button-prev"></div>
    <div v-show="showNavigation" ref="nextBtn" class="swiper-button-next"></div> -->

    <!-- my custom navigation button -->
    <div v-show="showNavigation" ref="prevBtn" class="swiper-button--prev">&lt;</div>
    <div v-show="showNavigation" ref="nextBtn" class="swiper-button--next">&gt;</div>

    <!-- If we need scrollbar -->
    <!-- <div class="swiper-scrollbar"></div> -->
  </div>
</template>


<script>

export default {
  data() {
    return {
    };
  },

  props: {
    opt: {
      type: Object,
      default: null
    }
  },

  computed: {
    windowWidth() {
      return this.$store.state.windowWidth;
    },

    showNavigation() {
      return this.windowWidth > 574;
    }
  },

  mounted() {
    const opt = Object.assign({}, this.getDefOptions(), this.opt );
    this._swiper = new Swiper(this.$el, opt);
  },

  updated() {
    clearTimeout(this._timeout);
    this._timeout = setTimeout(() => {
      this._swiper.update();
    }, 20);
  },

  beforeDestroy() {
    if (this._swiper) {
      this._swiper.destroy();
    }
  },

  methods: {
    getDefOptions() {
      return {
        // observer: true,
        slidesPerView: 2,
        slidesPerGroup: 2,
        spaceBetween: 20,
        freeMode: true,
        // autoHeight: true,
        // grabCursor: true,
        // mousewheel: true,

        navigation: {
          nextEl: this.$refs.nextBtn,
          prevEl: this.$refs.prevBtn
        },

        // And if we need scrollbar
        // scrollbar: {
        //   el: '.swiper-scrollbar'
        // },

        breakpoints: {
          670: {
            slidesPerView: 3,
            slidesPerGroup: 3,
            spaceBetween: 30
          },
          1280: {
            slidesPerView: 4,
            slidesPerGroup: 4,
            spaceBetween: 30
          }
        }
      };
    }
  }
};
</script>

<style lang="scss">
$nav-btn-size: 2rem;

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.2s;
}

.swiper-slide > :first-child {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  max-width: 100%;
}

.swiper-slide:hover {
  transform: scale(1.05);
  opacity: 0.8;
}

.swiper-button-prev,
.swiper-button-next {
  color: darken(rgb(255, 0, 0), 10);
  transition: 0.2s;
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  color: rgb(255, 0, 0);
}

.swiper-container {
  height: 250px;
  // margin-top: 2rem;
}

/* custom navigation button */
.swiper-button--prev, .swiper-button--next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $nav-btn-size;
  height: $nav-btn-size;
  position: absolute;
  color: red;
  font-size: 2.5rem;
  top: 50%;
  z-index: 100;
  transform: translateY(-50%);
  border-radius: 50%;
  cursor: pointer;
  transition: 0.2s;
  background-color: rgba(255,255,255,0.2);
  box-shadow: 0 0 5px 1px rgba(0,0,0,0.2);;
}

.swiper-button--prev:hover, .swiper-button--next:hover {
  background-color: rgba(255,255,255,0.7);
}

.swiper-button--prev.swiper-button-disabled, .swiper-button--next.swiper-button-disabled {
  opacity: 0.5;
}

.swiper-button--prev {
  left: 0.5rem;
}

.swiper-button--next {
  right: 0.5rem;
}

@media screen and (max-width: 768px) {
  .swiper-container {
    height: 150px;
    // margin-top: 2rem;
  }
}
</style>
