<template>
  <div  class="responsive-wrapper">
    <iframe allowFullScreen frameBorder="0" :src="url"></iframe>
  </div>
</template>

<script>
export default {
  props: {
    ratio: {
      type: Array,
      default: () => [16, 9]
    },
    url: {
      type: String,
      default: ''
    }
  },

  mounted() {
    const [width, height] = this.ratio;
    this.$el.style.paddingBottom = (height/width)*100 + '%';
  }
};
</script>

<style lang="scss" scoped>
.responsive-wrapper {
  position: relative;
  /* 16:9 */
  // padding-bottom: 56.25%;
  overflow: hidden;
}

.responsive-wrapper > :first-child {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>
