
function fetchVideos() {
  return fetch('./data.json').then((res) => res.json());
}

function fetchNews() {
  // query string: pid=XXXXXX
  return fetch('https://uat-h5.nownews.com/nn-client/api/v1/cat/entertainment/').then((res) => res.json());
}

function fetchReports() {
  return fetch('https://gcs-static-json-lb.nownews.com/api/v1/client/post/categoryItem.json').then((res) => res.json());
}

export {fetchVideos, fetchNews, fetchReports};
